document.addEventListener('DOMContentLoaded',(()=>{'use strict';const tables=document.querySelectorAll('.table-datatable');if(tables){class KUdatatable{constructor(table){this.datatable=table,this.initDatatable()}initDatatable(){this.datatable=new DataTable(this.datatable,{responsive:!0,language:{url:getLang()}})}}Array.from(tables).forEach((table=>{new KUdatatable(table)}))}}));